import amap from '../../../../common/amap-wx.js'
import MxDatePicker from '../../../../components/uni/mx-datepicker/mx-datepicker.vue'
export default{
	data(){
		return{
			showPicker: false,
			isShow:false,
			modalName:null,
			 value: '',
			CustomBar: this.CustomBar,
			shop:{
				id:'',
				name:''
			},
			
			amapPlugin:null,//地图组件
			key:'4c523fb1857f99ba7f2d683d9e88ec1e',//地图key
			shopList:[],
			designer:{
				name:'',
				telephone:'',
				date:''
			},
			repairObj:{
				bigName:'',
				bigID:'',
				subName:'',
				subID:'',
				summary:''
			},
			type:'rangetime',
			files:[],
			imgList:[],
			address:'',
			token:''
		}
	},
	components:{
		MxDatePicker
	},
	getOpenDate(){
		var date=new Date();
		return this.format(date,'YMD')
	},
	onLoad(){
		//进行定位
		this.amapPlugin=new amap.AMapWX({
			key:this.key
		})
		this.getLocationInfo();
		this.getUploadToken();
		this.$fire.on('shop',result=>{
			this.shop.id=result.id;
			this.shop.name=result.name;
		})
		this.$fire.on('repair',result=>{
			this.repairObj={
				bigID:result.bigID,
				bigName:result.bigName,
				subID:result.subID,
				subName:result.subName
			}
		})
		uni.getStorage({
			key:'userInfo',
			success: (res) => {
				
				this.designer.name=res.data.name;
				this.designer.telephone=res.data.account;
			}
		})
		
		
	},
	methods:{
		//提交报修
		createOrder(){
			
			
			if(!this.repairObj.bigID){
				uni.showToast({
					title:'请选择报修类别',
					icon:'none'
				})
			}else if(!this.repairObj.summary){
				uni.showToast({
					title:'请输入报修描述',
					icon:'none'
				})
			}else{
				uni.getStorage({
					key:'userInfo',
					success: (res) => {
						this.$ajax('NewServiceOrder',{
							catalog:this.repairObj.subID?this.repairObj.subID:this.repairObj.bigID,
							creator:res.data.id,
							shop:this.shop.id,
							appointdate:this.designer.date?this.designer.date:this.getOpenDate,
							summary:this.repairObj.summary,
							files:this.files?this.files.join(','):'',
							contractor:this.designer.name?this.designer.name:res.data.name,
							telephone:this.designer.telephone?this.designer.telephone:res.data.account
						},res=>{
							 uni.showToast({
							    title:'新增报修成功',
							    icon:'success'
							});
							setTimeout(()=>{
							    uni.navigateTo({
							        url:'../shop-center'
							    })
							},500)
						})
						
					}
				})
				
		//         uni.request({
		//             url:this.$store.state.url+'NewServiceOrder',
		//             data:{
		//                 // owner:this.$store.state.userInfo.owner,
		// 				owner:18,
		// 				userId:49,
		//                 catalog:this.repairObj.subID,
		//                 // creator:this.$store.state.userInfo.id,
		// 				creator:49,
		// 				// creator:49,
		//                 shop:65,
		//                 // shop:65,
		//                 appointdate:this.designer.date?this.designer.date:this.getOpenDate,
		//                 summary:this.repairObj.summary,
		//                 files:this.files?this.files.join(','):'',
		//                 contractor:this.designer.name?this.designer.name:this.$store.state.userInfo.name,
		//                 telephone:this.designer.telephone?this.designer.telephone:this.$store.state.userInfo.mobile
		// 
		//             },success: (res) => {
		//                 uni.showToast({
		//                     title:'新增报修成功',
		//                     icon:'success'
		//                 });
		//                 setTimeout(()=>{
		//                     uni.navigateTo({
		//                         url:'../shop-center'
		//                     })
		// 					uni.clearStorage()
		//                 },500)
		//             }
		//         })
			}
		},
		toRepairItem(){
			uni.navigateTo({
				url:'../repair-item/repair-item'
			})
		},
		//获得上传图片的token
		getUploadToken(){
			uni.request({
				url:this.$store.state.url+'UploadToken',
				success: (res) => {
					this.token=res.data.data
				}
			})
		},
		ChooseImageEvent(){
				uni.chooseImage({
					count:9,
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						const tempFilePaths=res.tempFilePaths;
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
								this.imgList = res.tempFilePaths
						}
						for(var i=0;i<res.tempFilePaths.length;i++){
							var  uploadTask=uni.uploadFile({
								url:this.$store.state.uploadHostUrl+this.token,
								filePath:tempFilePaths[i],
								name:'file',
								formData:{
									'x:type':18,
									'x:owner': 18,
									'x:creator': 49,
								},
								success: (uploadFileRes) => {
									let res=JSON.parse(uploadFileRes.data);
									this.files.push(res.data);
								}
							});
							uploadTask.onProgressUpdate((res)=>{
								if(res.progress==100){
									uni.showToast({
										title:'上传成功',
										icon:'none'
									})
								}
							},(error)=>{
								uni.showToast({
									title:'上传失败',
									icon:'none'
								})
							})
						}
						
						
					}
				})
		
		},
		DelImg(event){
			uni.previewImage({
				urls: this.imgList,
				current: event
			});
		},
		ViewImage(event){
			uni.showModal({
						content: '确定删除？',
						cancelText: '取消',
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								this.imgList.splice(event, 1);
								this.$ajax('RemoveFiles',{
									id:this.files[event],
								},res=>{
									this.files.splice(event,1)
									uni.showToast({
										title:'删除成功',
										icon:'none'
									})
								})
								
							}
						}
					})
		},
		onSelected(e) {//选择
		    this.showPicker = false;
		    if(e) {
		        this[this.type] = e.value;
		        this.designer.date=e.value.replace(/\//g,'-');
				console.log(this.designer.date)
				// uni.setStorage({
				// 	key:'time',
				// 	data:e.value
				// })
		    }
		},
		 onShowDatePicker(type){//显示
		    this.type = type;
		    this.showPicker = true;
		    this.value = this[type];
		},
		//显示最近的门店
		toNearShopList(){
			uni.navigateTo({
				url:'../near-shop-list/near-shop-list?address='+this.address
			})
		},
		//获得位置
		getLocationInfo(){
			uni.showLoading({
				title:'获取位置中..',
			})
			this.amapPlugin.getRegeo({
					success:(data)=>{
						this.address=`${data[0].regeocodeData.addressComponent.province}${data[0].regeocodeData.formatted_address}`
						uni.hideLoading();
						uni.showToast({
							title:'获取位置成功',
							icon:'none'
						})
						
					},
					fail:(res)=>{
						// console.log(res)
					}
			})
		},
		showModal(e) {
			console.log(e)
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(){
			this.modalName=''
		},
		//验证电话号码
		checkTelEvent(event){
			if(!(/^1[3|5|7|8][0-9]\d{4,8}$/.test(event))){ 
				uni.showToast({
					title:'手机号码不存在',
					icon:'none'
				})
				return false; 
			} 
		}
	}
}
